	module.exports = {"data":{"markdownRemark":{"html":"<p>I love design. And I love practicing design. And as an elearning devleoper that often translates into visuals, either graphics or video. So when I get talking about design, I think I sometimes give the impression that visuals are all I care about, that I hate text, that text is an evil, demonic nightmare (or at least something to be avoided). But that’s just not true. I love words. I love language. So I thought it was high time that I explored how I approach text as an instructional designer.</p>\n<h2>Words Matter</h2>\n<p>So what’s with all of the anti-text rhetoric in the ID sphere ? I think that the main desires for anyone designing elearning (or any screen experience) are to create experiences that have plain, understandable language, that are presented in easily digested chunks and, most importantly, that engage. So I think that we tend to discuss words with our SMEs or clients in a way that steers them away from dumping piles of words (as though that will help a user perform better) simply to get them to think about, “How can I say this more plainly or more simply? How can I break this down into its most essential parts? How can we help users do what we need them to be able to do?” It’s about choosing the right words—the essential words—giving them context, and encouraging practice.</p>\n<h2>And Design Matters, Too</h2>\n<blockquote>\n<p>The takeaway for me from these three articles is that the way we design our text is just as important in providing a readable, engaging, and enlightening experience as the words themselves.</p>\n</blockquote>\n<p>But sometimes, we are called on to engage in information design, instead (<a href=\"/blog/information-design-and-digital-magazines/\" title=\"Information Design and Digital Magazines\">something that I’ve explored previously</a>). When that’s the case, we aren’t designing performance-changing exercises so much as we’re trying to bring clarity to information. This often involves a mixture of text and visuals. One of the cool things that has influence me is the increased (to me, anyway) emphasis on typography and creating a beautiful reading experience online. Take the Medium platform, for example. People write long, wonderful essays on Medium. But they are designed such that they are both effective pieces of communication and a pleasure to read.</p>\n<h2>Examples</h2>\n<figure>\n  <a href=\"https://thenib.com/lighten-up-4f7f96ca8a7e\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Panel from lighten up\" title=\"\" src=\"/static/lightenup-3039436bbe16b2ec29ae5d363e0b97ea-aabb5.jpeg\" srcset=\"/static/lightenup-3039436bbe16b2ec29ae5d363e0b97ea-2320f.jpeg 203w,\n/static/lightenup-3039436bbe16b2ec29ae5d363e0b97ea-427c1.jpeg 405w,\n/static/lightenup-3039436bbe16b2ec29ae5d363e0b97ea-aabb5.jpeg 810w,\n/static/lightenup-3039436bbe16b2ec29ae5d363e0b97ea-539de.jpeg 1000w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n  </a>\n  <figcaption>Ronald Wimberly wrote a wonderful essay using words and drawings.</figcaption>\n</figure>\n<figure>\n  <a href=\"https://medium.com/@webalys/how-to-create-a-collection-with-thousands-of-icons-5051a613511c\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 68.44626967830253%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"collection of iconcollectioninspiration\" title=\"\" src=\"/static/iconcollectioninspiration-64a18b7f099f572b1e861f34eaac2220-1626e.png\" srcset=\"/static/iconcollectioninspiration-64a18b7f099f572b1e861f34eaac2220-076ec.png 203w,\n/static/iconcollectioninspiration-64a18b7f099f572b1e861f34eaac2220-7ae34.png 405w,\n/static/iconcollectioninspiration-64a18b7f099f572b1e861f34eaac2220-1626e.png 810w,\n/static/iconcollectioninspiration-64a18b7f099f572b1e861f34eaac2220-9af38.png 1215w,\n/static/iconcollectioninspiration-64a18b7f099f572b1e861f34eaac2220-87b52.png 1461w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n  </a>\n  <figcaption>Vincent LeMoign made&#xA0;described his icon&#xA0;creation process</figcaption>\n</figure>\n<figure>\n  <a href=\"https://readymag.com/shuffle/dieter-rams/products/\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 49.739583333333336%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"10 Principles for Good Design\" title=\"\" src=\"/static/dieterrams-fd32aad1cae9688b54907469b12b12ec-1626e.png\" srcset=\"/static/dieterrams-fd32aad1cae9688b54907469b12b12ec-076ec.png 203w,\n/static/dieterrams-fd32aad1cae9688b54907469b12b12ec-7ae34.png 405w,\n/static/dieterrams-fd32aad1cae9688b54907469b12b12ec-1626e.png 810w,\n/static/dieterrams-fd32aad1cae9688b54907469b12b12ec-9af38.png 1215w,\n/static/dieterrams-fd32aad1cae9688b54907469b12b12ec-03d88.png 1620w,\n/static/dieterrams-fd32aad1cae9688b54907469b12b12ec-a5ec7.png 1920w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n  </a>\n  <figcaption> This piece from Shuffle Magazine on Dieter Rams 10 Principles of Design combines a full-screen experience of text with parallax scrolling and other media.</figcaption>\n</figure>\n<p>The takeaway for me from these three articles is that the way we design our text is just as important in providing a readable, engaging, and enlightening experience as the words themselves. These articles used images, they used color, they used size and proximity and weight. All of these things made the articles easily scannable and highlighted the most important bits. They made the text beautiful. They gave it movement. They communicated. That’s what the right words in the right design can accomplish.</p>\n<h2>And, You Know What, Story Matters Most</h2>\n<p>Every time I play a Twine game,or another piece of interactive fiction, I’m reminded of how powerful and engaging words can be when they make a story, particularly when they make a story that puts the user smackdab in the center of that story. <a href=\"http://emshort.home.mindspring.com/Alabaster/\" title=\"Alabaster\">Try playing this piece of IF by Emily Short</a> and tell me how boring text is. (Bet you can’t!)</p>\n<figure>\n  <a href=\"http://emshort.home.mindspring.com/Alabaster/\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 23.543123543123542%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Alabaster title screen\" title=\"\" src=\"/static/alabaster-6c935e40a7c5b99d5bc52a5bc1bb0f6e-1626e.png\" srcset=\"/static/alabaster-6c935e40a7c5b99d5bc52a5bc1bb0f6e-076ec.png 203w,\n/static/alabaster-6c935e40a7c5b99d5bc52a5bc1bb0f6e-7ae34.png 405w,\n/static/alabaster-6c935e40a7c5b99d5bc52a5bc1bb0f6e-1626e.png 810w,\n/static/alabaster-6c935e40a7c5b99d5bc52a5bc1bb0f6e-f84c9.png 858w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n  </a>\n</figure>\n<h2>Respect the Text</h2>\n<p>Used correctly and designed beautifully, words are as powerful as they ever were. So when we seek to use less of them, it’s always in a desire to distill what is essential. We respect the text.</p>","frontmatter":{"date":"April 16, 2015","path":"/blog/respect-the-text","title":"Respect the Text","tags":["instructional design","text","words"],"categories":["Design"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.7777777777777777,"src":"/static/Instructional-Designing-Words-51ec1a2690c949832101b9cf7d0021ef-c9de9.png","srcSet":"/static/Instructional-Designing-Words-51ec1a2690c949832101b9cf7d0021ef-aafd1.png 203w,\n/static/Instructional-Designing-Words-51ec1a2690c949832101b9cf7d0021ef-c6a3b.png 405w,\n/static/Instructional-Designing-Words-51ec1a2690c949832101b9cf7d0021ef-c9de9.png 560w","sizes":"(max-width: 560px) 100vw, 560px"}}}}}},"pathContext":{}}

